                        </div>
                    </div>
                    <a  class="main-quiz-question-global-btn--elem" @click="next()" v-show="countdown !== 0 || countdownBlocker === true">next<i class="las la-arrow-right"></i></a>
                    <a  class="main-quiz-question-global-btn--elem __block" v-show="countdown == 0 && countdownBlocker === false">next<i class="las la-arrow-right"></i></a>
