<script>
/* eslint-disable */
import axios from 'axios';
import moment from 'moment';

export default {
    data() {
        return {
            id: this.$route.params.id,
            quiz: [],//quiz,
            // Store current question index
            questionIndex: 0,
            // An array initialized with "false" values for each question
            // It means: "did the user answered correctly to the question n?" "no".
            userResponses: [],//Array(quiz.questions.length).fill(false),
            showScore: false,
            score: 0,
            shuffledData: [],
            answerTable: [],
            toggleScoreAreas: true,

            // Cowntdown Varibales
            defaultNumber: 60,
            countdown: null,
            countdownTimer: null,
            countdownBlocker: false,
            barWidth: 0,

            // Answers Varibales
            tags: {
                icon: "005-ribbon-3.svg",
                title: "Nice!",
            },

            contentLoaded: false
        }
    },
    created() {
        this.shuffleArrData();
        // this.countDownTimerFn();
    },
    computed: {
        timeOut: function(){
            return this.countdownTimer.format('mm:ss');
        }
    },
    methods: {
        async shuffleArrData(){
            try {
                const data = await axios({
                    method: "GET",
                    url: `/api/v1/quiz/${this.id}`
                });

                this.quiz = data.data.data.data;

                this.getShuffledArray(this.quiz);

                this.showScore = true;
            } catch (error) {
                return this.$router.push('/error');
            }
            this.contentLoaded = true;
        },

        next() {  
            this.setCountDownData();
            this.getUserAnswersFn();
            this.questionIndex++;

            this.getUserDegreeData();
        },
        prev() { this.questionIndex--; },
        startAgain() {
            this.questionIndex = 0;
            this.score = 0;
            this.answerTable = [];
            this.toggleScoreAreas = true;
            this.getShuffledArray(this.quiz);
        },
        
        updateUserResponces(e, i, val) {
            this.removeOnFocusClass();
            e.target.classList.add("isClicked");

            this.userResponses[i] = val;
            this.score = this.userResponses.filter(val => val).length
        },
        removeOnFocusClass(){
            const elems = document.querySelectorAll(".response--area");
            [].forEach.call(elems, function(el) {
                el.classList.remove("isClicked");
            });
        },

        //
        countDownTimerFn() {
            let _sec = (!this.countdownBlocker) ? 1000 : 0 ;

            if(!this.countdownBlocker){
                if( this.questionIndex <= (this.shuffledData.length - 1) ){
                    if(this.countdown > 0) {
                        setTimeout(() => {
                            this.countdown -= 1;
                            this.countdownTimer = moment(this.countdownTimer.subtract(1, 'seconds'));
                            this.barWidth = (100 * (this.defaultNumber - this.countdown)) / this.defaultNumber;
                            
                            this.countDownTimerFn();
                        }, _sec);
                    }
                    
                    if(this.countdown == 0) {
                        setTimeout(() => {
                            this.next();
                            this.countDownTimerFn();
                        }, _sec);
                    }
                }
            }
        },
        setCountDownData(){
            let num = this.defaultNumber; // seconds
            this.countdown = num;
            this.countdownTimer = moment(num * 1000);
            this.barWidth = 0;
        },
        ableCountDown(){
            this.countdownBlocker = !this.countdownBlocker;
            if(!this.countdownBlocker) this.countDownTimerFn();
        },

        // Set Global Functions:
        // Collect User Response & Anwsers
        getUserAnswersFn(){
            this.removeOnFocusClass();

            let _i = this.questionIndex;

            const obj = {};
            obj.title = (this.shuffledData[_i].title) ? this.shuffledData[_i].title : null;
            obj.correctAnswer = this.shuffledData[_i].responses.filter(el => el.correct === true);
            obj.isCorrect = this.userResponses[_i];
            let req = obj;

            this.answerTable.push(req);
        },
        getShuffledArray(_array){
            // Definde Array contain only [false] value based on Array length
            this.userResponses = Array(_array.questions.length).fill(false);

            // // Shuffle question responses randomly
            this.shuffledData = _array.questions.map(el => {
                el.responses.sort(() => .5 - Math.random())
                return el;
            });

            //
            this.setCountDownData();
            this.countDownTimerFn();
        },
        getUserDegreeData(){
            let total = this.shuffledData.length;
            let average = Math.ceil(( this.score * 100 ) / ( total ));
            console.log(this.score, total, average);
            if(average >= 95.1 && average <= 100){
                this.tags.icon = "001-trophy.svg";
                this.tags.title = "Congratulations!";
            }
            else if(average >= 85.1 && average <= 95){
                this.tags.icon = "003-ribbon-1.svg";
                this.tags.title = "Great job!";
            }
            else if(average >= 75.1 && average <= 85){
                this.tags.icon = "004-ribbon-2.svg";
                this.tags.title = "Great!";
            }
            else if(average >= 50 && average <= 75){
                this.tags.icon = "005-ribbon-3.svg";
                this.tags.title = "Nice!";
            }
            else{
                this.tags.icon = "009-error.svg";
                this.tags.title = "Oops .. you're failed!";
            }
        }
    }
}
</script>

